// ç¼–è¯‘ HTML å…ƒç´ 
// import Watcher from './Watcher'

class Compile {
    constructor(el, vm) {
        // å–å·²åšå¥½æ‹¦æˆªæ“ä½œçš„æ•°æ®æº
        this.data = vm.$data;
        // èŽ·å–æ ¹èŠ‚ç‚¹
        let root = this.root = document.querySelector(el);
        // èŽ·å–æ ¹èŠ‚ç‚¹ä¸‹æ‰€æœ‰å­èŠ‚ç‚¹é›†åˆ
        let nodes = root.childNodes;
        this.nodes = nodes;
        // å­˜å‚¨å…¨å±€ this
        this.vm = vm;
        // è®¢é˜…åˆ—è¡¨
        this.binding = vm.binding;
        // å¼€å§‹ç¼–è¯‘
        this.compile(nodes);
    }
    // ç¼–è¯‘å‡½æ•°
    compile(nodes) {
        let vm = this.vm;
        let data = vm.$data;
        console.log(nodes)
        // éåŽ†å­èŠ‚ç‚¹
        nodes.map(node => {
            // èŽ·å–èŠ‚ç‚¹ç±»åž‹
            // 1: å…ƒç´ èŠ‚ç‚¹  3ï¼š å†…å®¹èŠ‚ç‚¹
            // https://developer.mozilla.org/zh-CN/docs/Web/API/Node/nodeType
            let type = node.nodeType;
            // æ–‡å­—èŠ‚ç‚¹
            if (type === 3) {
                let text = node.textContent.trim();
                if (!text) return true;
                this.compileText(node, 'textContent')
            }
            // ç¼–è¯‘å…ƒç´ èŠ‚ç‚¹
            else if (type === 1) {
                // å¦‚æžœæœ‰å­èŠ‚ç‚¹åˆ™å…ˆæ”¾å…¥é€’å½’
                if (node && node.children.length > 0) this.compile(node);
                // å¤„ç† input æˆ– textarea ä¸­çš„ v-model
                if (node.hasAttribute('v-model') && (node.tagName === 'INPUT' || node.tagName === 'TEXTAREA')) {
                    node.addEventListener('input', (() => {
                        let key = node.geteAttribute('v-model');
                        // åŠ å…¥è¯¥é¡¹è‡³ç›‘å¬é˜Ÿåˆ—
                        this.vm.binding.addSub(new Watcher(node, 'value', data, key));
                        // åˆ é™¤è¯¥é¡¹å±žæ€§
                        node.removeAttribute('v-model');
                        // ä¿®æ”¹å€¼
                        return () => {
                            data[key] = node.value;
                        }
                    })());
                }
                // å¤„ç† v-html 
                if (node.hasAttribute('v-html')) {
                    let key = node.geteAttribute('v-html');
                    this.vm.binding.addSub(new Watcher(node, 'innerHTML', data, key))
                    node.removeAttribute('v-html');
                }
                // å¤„ç†ç‚¹å‡»äº‹ä»¶ onClick || @click || onTap || @tap
                if (node.hasAttribute('onClick') || node.hasAttribute('@click')) {
                    let key = node.geteAttribute('onClick') || node.getAttribute('@click');
                    // è½¬å‘è‡³å¯¹åº”å¤„ç†æ–¹æ³•
                    let method = vm.options.method[key].bind(data);
                    // ç»‘å®šç‚¹å‡»äº‹ä»¶
                    node.addEventListener('click', method);
                    // åˆ é™¤å¯¹åº”çš„å±žæ€§
                    node.hasAttribute('onClick') && node.removeAttribute('onClick');
                    node.hasAttribute('@click') && node.removeAttribute('@click');
                }
                this.compileText(node, 'innerHTML');
            }

        })
    }
    // ç¼–è¯‘æ–‡å­—èŠ‚ç‚¹
    compileText(node, type) {
        let reg = /\{\{(.*?)\}\}/;
        let txt = node.textContent;
        let data = vm.$data
        // ç¼–è¯‘åŒèŠ±æ‹¬å·
        if (reg.test(txt)) {
            node.textContent = txt.replace(reg, (matched, value) => {
                // è®¢é˜…èŠ±æ‹¬å·åŒ…è£¹å†…å®¹
                this.vm.binding.addSub(new Watcher(node, type, data, key))
                // è¿”å›žå¯¹åº”å€¼
                // value æ˜¯æ­£åˆ™ç¬¬ä¸€ä¸ªæ‹¬å·åŒ¹é…åˆ°çš„å­ä¸²
                if (value.split(',').length > 1) {
                    let v = null;
                    // è¿”å›žèŠ±æ‹¬å·ä¸­çš„å±žæ€§çš„å€¼
                    value.split(',').forEach((val, i) => {
                        v = !v ? vm[val] : v[val];
                    })
                    return v;
                } else {
                    return vm[value];
                }
            })
        }
    }
}